@import "./scss/Variable";
@import "./scss/mixin";

:root {
  --color-light-text: #{$color-light-text};
  --color-lighter-text: #{$color-lighter-text};
  --color-light-back: #{$color-light-back};
  --color-lighter-back: #{$color-lighter-back};
  --color-btn: #{$color-btn};
  --color-btn-active: #{$color-btn-active};
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
html {
  font-size: 62.5%;
}
// geomanist, "inter ui", -apple-system, "system-ui", roboto, "segoe ui", Helvetica, Arial, sans-serif
// for para
// Bookerly, Palatino, "Book Antiqua", serif;
// for heading
body {
  font-family: geomanist, "inter ui", -apple-system, "system-ui", roboto,
    "segoe ui", Helvetica, Arial, sans-serif;
  color: var(--color-light-text);
  background-color: var(--color-lighter-back);
}

.container {
  height: 100vh;
  @include display(column);
  justify-content: space-between;
}

////////////HEADER////////////
.navbar {
  font-size: 2rem;
  padding: 3rem 6rem;
  @include display(row);
  justify-content: space-between;
  background-color: var(--color-light-back);

  @media (max-width: 400px) {
    justify-content: center;
  }

  &--list {
    @include display(row);
    align-items: center;

    &--unorder {
      @include display(row);
      list-style-type: none;
    }

    &--item {
      padding: 0 2rem;

      &:hover {
        @include scale();
      }
    }
  }
  & a {
    text-decoration: none;
    color: var(--color-light-text);
  }

  &--home {
    @include display(row);
    font-weight: 700;

    @include respond(phone) {
      margin: 0 2rem;
    }

    &:hover {
      @include scale();
    }
  }
}
///////////HOME PAGE/////////////////
.btn--mode {
  margin: 0 2rem;
  width: 2.5rem;
  height: 2.5rem;
  background-image: linear-gradient(
    to right,
    var(--color-light-back) 0 50%,
    var(--color-light-text) 50% 100%
  );
  border: 0.3rem solid var(--color-light-text);
  border-radius: 50%;
}

.main--page {
  height: 80vh;
  font-size: 3.2rem;
  @include display(column);
  justify-content: center;
  align-items: center;
  text-align: center;

  &--heading {
    font-family: Bookerly, Palatino, "Book Antiqua", serif;
    text-align: center;
  }

  &--para {
    margin: 1rem;
  }

  &--links {
    @include display(row);

    & svg {
      margin: 0 1rem;
      height: 4rem;
      width: 4rem;

      & path {
        fill: var(--color-light-text);
      }

      &:hover {
        @include scale();
      }
    }
  }
}

///////////FOOTER/////////////////
.footer {
  @include display(row);
  align-items: center;
  height: 4rem;
  font-size: 1.2rem;
  justify-content: center;

  &--developer {
    padding: 0 0.5rem;
    color: var(--color-light-text);

    &:hover {
      @include scale();
    }
  }
}
////////////BLOGS////////////////
main {
  @include display(column);
  justify-content: center;
  align-items: center;
  padding: 4rem 0;
}

.blogs {
  &--box {
    width: 70vw;
    @include respond(phone) {
      width: 100vw;
    }
    @include respond(tab-port) {
      width: 90vw;
    }
    @include respond(tab-land) {
      width: 80vw;
    }
  }

  &--heading {
    font-size: 4.8rem;
    font-family: Bookerly, Palatino, "Book Antiqua", sans-serif;
    font-weight: 900;
    margin-bottom: 4rem;

    @include respond(phone) {
      margin-left: 1rem;
    }
    @include respond(tab-port) {
      font-size: 4rem;
    }
  }

  &--post {
    @include display(row);
    width: 95%;
    align-self: flex-end;
    margin-bottom: 6rem;

    @include respond(tab-land) {
      width: 98%;
      align-self: center;
    }

    &--box {
      @include display(column);
    }
    &--year--box {
      width: 100%;
    }

    &--div--topic {
      @include display(row);
      justify-content: space-between;
      font-size: 2.2rem;
      padding: 1rem 0 2rem 0;
      border-bottom: 2px dashed var(--color-lighter-text);

      @include respond(tab-port) {
        @include display(column);
        font-size: 2rem;
      }

      &--heading {
        font-weight: 400;
        width: 80%;
        line-height: 2.5rem;

        @include respond(tab-port) {
          margin-bottom: 0.8rem;
          width: 100%;
        }

        & a {
          text-decoration: none;
          color: var(--color-light-text);
        }
      }
      &--date {
        color: var(--color-lighter-text);
        font-weight: 400;

        @include respond(tab-port) {
          align-self: flex-end;
        }
        @include respond(tab-land) {
          font-size: 1.8rem;
        }
        @media (max-width: 400px) {
          display: none;
        }
      }
    }
  }

