.spinner {
  display: flex;
  justify-content: center;
}
////////////BLOG/////////////////

.blog {
  width: 70vw;

  @include respond(tab-port) {
    width: 85vw;
  }

  &--article {
    &-title {
      font-size: 2.4rem;
      margin-bottom: 2rem;
    }
    &-info {
      font-size: 1.2rem;
      margin: 2rem;
      @include display(row);
      justify-content: space-between;

      &-page {
        @include display(row);
        align-items: center;
        font-size: 1.2rem;
        margin-left: 2rem;
      }

      &-detail {
        @include display(row);
        align-items: center;
      }
    }
    &-content {
      font-size: 2rem;
      line-height: 3.2rem;
      margin: 2rem;
    }
  }

  &--icon {
    width: 2rem;
    height: 2rem;
    margin: 0 0.5rem;
  }
}
